.Photos-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 293px));
    -ms-flex-pack: center;
        justify-content: center;
    grid-gap: 28px;
    margin: 10px;
}

.Photos {
    cursor: pointer;
    position: relative;
    display: block;
    padding: 15px;
    -webkit-box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.548);
            box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.548);
}

.Photo-image {
    margin: 0;
}

.Photo-image img {
    width: 100%;
    vertical-align: top;
    border-radius: 3px;
    -webkit-box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.363);
            box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.363);
}

